import React, { useState, useEffect, useCallback } from 'react';
import { Table, Drawer, Button, message, Form, Input, Modal, Select } from 'antd';
import { Icon, QueryFields } from "@/components";
import Apis from "@/apis";
const confirm = Modal.confirm;
const initData = {
  title: '',
  name: '',
  tbName: '',
  fromTable: '',
  queryFields: [],
  resultFields: []
}

const AppView = (props) => {
  const [form] = Form.useForm();
  // const formRef = useRef()
  const [tableList, setTableList] = useState([]);
  const [keyOptions, setKeyOptions] = useState([]);
  const [valOptions, setValOptions] = useState([]);
  const [tbOptions, setTbOptions] = useState([]);
  const [show, setShow] = useState()
  const [formData, setFormData] = useState(initData)
  const { onClose, viewData, ...otherProps } = props

  const getData = useCallback(async () => {
    let res = await Apis.dictionaryOptions({ _id: '60a72c741b2e6d29e0760e89' })
    if (res) setTbOptions(res.map(item => ({ label: item.key, value: item.value })))
    if (viewData && Array.isArray(viewData.components)) {
      let components = viewData.components.filter(item => item.__component === 'view.editview')
      let _tableList = []
      for (let item of components) {
        let res = await Apis.appList({ type: 'form_data', tbName: item.tbName }, 'customize-funs')
        if (res) {
          _tableList.push({
            title: item.title,
            tbName: item.tbName,
            valOptions: item.items.map(subItem => ({ label: subItem.label, value: subItem.name, })),
            list: res
          })
        }
      }
      setTableList(_tableList)
    }
  }, [viewData])

  useEffect(() => {
    console.warn('');
    getData()
  }, [getData])

  const handleClick = (item, tbName) => {
    setFormData({ ...item })
    form.setFieldsValue({ ...item })
    let hasOne = tableList.find(tb => tb.tbName === tbName)
    if (hasOne) {
      setValOptions(hasOne.valOptions)
    }
    if (item.fromTable) handleChange(item.fromTable)
    setShow(true)
  }

  const handleTbChange = name => {
    let hasOne = tableList.find(item => item.tbName === name)
    if (hasOne) setValOptions(hasOne.valOptions)
  }

  const handleChange = async name => {
    let resp = await Apis.contentTypesConfig(name)
    if (resp.data && resp.data.contentType && resp.data.contentType.metadatas) {
      let result = Object.keys(resp.data.contentType.metadatas).map(key => {
        let item = resp.data.contentType.metadatas[key].edit
        return { label: item.label, value: key }
      })
      setKeyOptions(result.filter(item => !['id', 'createdAt', 'updatedAt', 'published_at'].includes(item.value)))
    }
  }

  const handleRemove = _id => {
    if (_id) {
      confirm({
        title: `?`,
        okText: '',
        cancelText: '',
        onOk: () => {
          Apis.appRemove({ _id, type: 'form_data', }, 'customize-funs').then(res => {
            if (res) {
              message.success('!')
              getData()
            } else {
              message.error('!')
            }
          })
        },
      });
    }
  }

  const handleAdd = () => {
    setFormData({ ...initData })
    form.setFieldsValue({ ...initData })
    setShow(true)
  }

  const handleSave = () => {
    let data = form.getFieldsValue()
    // console.warn(data, formData);
    if (formData._id) data._id = formData._id
    data.type = 'form_data'
    Apis[formData._id ? 'updateCustomizeFuns' : 'createCustomizeFuns'](data).then(res => {
      if (res) {
        message.success("!");
        getData()
      } else {
        message.error("!");
      }
    })
  }

  const getColumns = (tbName) => {
    return [
      {
        title: '',
        dataIndex: 'title',
        key: '0',
      },
      {
        title: '',
        key: '1',
        width: 120,
        render: item => (
          <div>
            <Button type="link" size="small" onClick={() => handleClick(item, tbName)}>
              
            </Button>
            <Button type="link" size="small" onClick={() => handleRemove(item._id, tbName)}>
              
            </Button>
          </div>
        )
      },
    ]
  }

  return (
    <Drawer
      title={''}
      placement="right"
      closable={true}
      width={400}
      zIndex={1000}
      bodyStyle={{ padding: 10 }}
      className="fieldSetting"
      onClose={onClose}
      footer={
        <div style={{ textAlign: 'center', }}>
          <Button onClick={handleAdd} type="link"
            icon={<Icon iconName={'Add'} marginRight={5} color="#007eff" />}
          >
            
          </Button>
        </div>
      }
      {...otherProps}
    >
      {
        tableList.length ? tableList.map((item, index) => (
          <Table
            columns={getColumns(item.tbName)}
            title={pageData => (tbOptions.find(opt => opt.value === item.tbName) || {}).label ?
              `${(tbOptions.find(opt => opt.value === item.tbName) || {}).label || ''}` : ''}
            size="middle"
            rowKey={record => record._id}
            dataSource={item.list}
            key={`table_${index}`}
          />
        )) : null
      }
      <Drawer
        title=""
        width={500}
        closable={false}
        onClose={() => setShow(false)}
        visible={show}
        footer={
          <div style={{ textAlign: 'center', }}>
            <Button onClick={handleSave} type="primary" style={{ marginRight: 8 }}>
              
            </Button>
            <Button onClick={e => setShow(false)}>
              
            </Button>
          </div>
        }
      >
        <Form layout="vertical" hideRequiredMark form={form} initialValues={formData}>
          <Form.Item name="title" label="">
            <Input />
          </Form.Item>
          <Form.Item name="name" label="">
            <Input />
          </Form.Item>
          <Form.Item name="fromTable" label="">
            <Select options={tbOptions} onChange={handleChange} />
          </Form.Item>
          {
            !formData.tbName ?
              <Form.Item name="tbName" label="">
                <Select options={tableList.map(item => ({ label: item.title, value: item.tbName }))} onChange={handleTbChange} />
              </Form.Item> : null
          }
          <Form.Item name="queryFields" label="">
            <QueryFields
              keyTitle=""
              valueTitle=""
              keyOptions={keyOptions}
              valOptions={valOptions}
            />
          </Form.Item>
          <Form.Item name="resultFields" label="">
            <QueryFields
              keyTitle=""
              valueTitle=""
              keyOptions={keyOptions}
              valOptions={valOptions}
            />
          </Form.Item>
        </Form>
      </Drawer>
    </Drawer>
  );
}
export default AppView